import './App.css';
import React, { Component } from 'react'
import {Route, Switch, HashRouter as Router} from 'react-router-dom';
import TopBar from './components/topBar/topBar';
import SideBar from './components/sidebar/sideBar';
import BookList from './components/main/bookList';
import Details from './components/details/details';
import Permission from './components/details/permission';


 
var url =  "https://java-library-api.herokuapp.com/";
export class App extends Component  {
  state = {
    menu: false,
    book: [],
    isFetching: true
  }


  componentDidMount(){
    this.fetchProducts();

  }

  fetchProducts = () =>{
    fetch(url + 'api/library/books/10/10')
    .then(res => res.json())
    .then(book => {
      this.setState({book, isFetching: false})
    });
  }

  sortBooksByName = () =>{
    var sorted = this.state.book.sort((a, b) => a.title.localeCompare(b.title));
    this.setState({books: sorted})
  }

  sortBooksByRating = () =>{
    var sorted = this.state.book.sort((a, b) => b.rating > a.rating ? 1 : -1);
    this.setState({books: sorted})
  }

  sortBooksByDate = () =>{
    var sorted = this.state.book.sort((a, b) => b.year.localeCompare(a.year));
    this.setState({books: sorted})
  }

  getTopBooks = () =>{
    fetch(url + 'api/library/books/popular/10')
    .then(res => res.json())
    .then(book => {
      this.setState({book})
    });
  }

  dicoverBooks = () => {
    fetch(url + 'api/library/books/discover')
    .then(res => res.json())
    .then(book => {
      this.setState({book})
    });
  }

  searchByName = (name) =>{
    fetch(url + `api/library/books/search/title/${name}`)
    .then(res => res.json())
    .then(book => {
      this.setState({book})
    });
  }

  windowSize = () =>{
    const { innerWidth: width } = window;
    return width < 750;
 }

  showMenu = () =>{
    if(this.state.menu === false){
      document.getElementById("sidebar").style.display = "block";
      this.setState({menu: true});
    }
    else{
      document.getElementById("sidebar").style.display = "none";
      this.setState({menu: false});
    }
    
  }

  render() {  
    return (
    <Router>
      <div className="grid-container">
        <div className="nav-bar-grid"> <TopBar className="topbar" menuMethod={this.showMenu} search={this.searchByName}/></div>
        <div className="side-bar-grid">
          <SideBar 
            nameSort={this.sortBooksByName}
            ratingSort={this.sortBooksByRating}
            dateSort={this.sortBooksByDate}
            topBooks={this.getTopBooks}
            discover={this.dicoverBooks}/>
        </div> 
        <div className="main-grid">
            <Switch>
              <Route path='/' exact render={() => <BookList books={this.state.book} isFetching={this.state.isFetching}/> } /> 
              <Route path={'/book/:id'}  render={() => <Details books={this.state.book} /> } />
              <Route path={'/alert'}  render={() => <Permission/> } />
            </Switch>
        </div>
        <div className="footer-grid"></div>
      </div>
   </Router>
  );
}
}

export default App;
